.intro {
	background-image: url('../img/intro/bg.png');
	background-size: cover;
	height: 800px;
	position: relative;
	h1 {
		font-size: 85px;
		letter-spacing: 2.1px;
		color: #ffffff;
		text-align: center;
		font-family: 'Austin';
		span {
			font-size: 60px;
			letter-spacing: 1.5px;
			display: block; }
		i {
			color: $periwinkle;
			font-style: normal;
			border-bottom: 2px dashed $periwinkle; } }
	a.free {
		display: block;
		width: 566px;
		height: 112px;
		border-radius: 51px;
		background-color: #ffb257;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 100px;
		text-decoration: none;
		padding-left: 50px;
		font-family: 'Circe';
		span {
			font-size: 18px;
			line-height: 1.33;
			letter-spacing: 1.4px;
			text-align: left;
			color: #000000;
			text-shadow: 0px -1px 2px rgba(255, 255, 255, 0.55);
			text-transform: uppercase;
			display: flex;
			align-items: center;
			height: 112px; }
		img {
			position: absolute;
			width: 202px;
			height: 200px;
			bottom: 0;
			right: 15px; } }
	&__course {
		width: 980px;
		height: 160px;
		border-radius: 20px;
		background-color: #1d1d1d;
		position: absolute;
		z-index: 10;
		bottom: -80px;
		left: 50%;
		transform: translateX(-50%);
		display: flex;
		align-items: center;
		h2 {
			font-family: Circe;
			font-size: 28px;
			letter-spacing: 2.8px;
			text-transform: uppercase;
			span {
				font-size: 13.5px;
				line-height: 1.44;
				letter-spacing: 1.4px;
				text-align: center;
				color: #aaaaaa; } }
		ul {
			li {
				font-size: 14px;
				letter-spacing: 1.1px;
				text-align: left;
				color: #ebebeb;
				position: relative;
				&:not(:first-child) {
					display: inline-block;
					padding-right: 20px; }
				&:before {
					content: '';
					background: url('../img/course/dot.png');
					display: block;
					width: 7px;
					height: 5px;
					position: absolute;
					left: -15px;
					top: 7px; } } }
		&__block {
			display: block;
			background-image: url('../img/course/bg.gif');
			background-size: cover;
			position: absolute;
			width: 300px;
			height: 160px;
			top: 0;
			right: 0;
			border-radius: 18px;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			text-decoration: none;
			img, span {
				position: relative;
				z-index: 10; }
			span {
				color: #ffaf6a; }
			&__black {
				position: absolute;
				width: 100%;
				height: 100%;
				background: rgba(0, 0, 0, 0.5);
				top: 0;
				border-radius: 18px; } } } }




