.container {
  width: 1200px !important;
  padding: 0 15px;
  height: 100%;
}

.row {
  height: 100%;
}

body {
  font-family: Circe;
  color: #2c2c2c;
}

h2 {
  font-size: 85px;
  letter-spacing: 2.1px;
  text-align: left;
  color: #ffffff;
  text-align: center;
  font-family: 'Austin';
}

h2 span {
  display: block;
  font-size: 60px;
  letter-spacing: 1.5px;
  text-align: left;
  color: #ffffff;
  text-align: center;
}

.content {
  padding: 50px 0;
}

.content h2 {
  color: #000000;
  font-size: 36px;
  margin-bottom: 20px;
}

.content h3 {
  margin-bottom: 10px;
}

.content p {
  font-size: 16px;
  margin-bottom: 10px;
}

.pop-up {
  position: fixed;
  overflow: hidden;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: none;
  z-index: 100;
}

.pop-up__block {
  position: absolute;
  overflow: visible !important;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.pop-up-login .pop-up__block {
  width: 500px;
}

.pop-up-login h2 {
  font-size: 30px;
}

.pop-up-close {
  position: absolute;
  top: -30px;
  right: -45px;
  cursor: pointer;
  width: 40px;
  height: 40px;
}

.pop-up-close:before, .pop-up-close:after {
  content: '';
  width: 40px;
  height: 2px;
  background: #ffffff;
  display: block;
}

.pop-up-close:before {
  transform: rotate(45deg) translate(2px, 2px);
}

.pop-up-close:after {
  transform: rotate(-45deg);
}

.support {
  padding: 20px 0;
}

.support h2 {
  color: #000000;
  font-size: 40px;
}

.support a {
  text-align: center;
  text-transform: uppercase;
  display: block;
  text-decoration: none;
  color: #0d426a;
  font-size: 30px;
}

.support a:hover {
  text-decoration: underline;
}
