.footer {
  background: #1e1d1e;
  hr {
    border: 1px solid #292829;
    margin-bottom: 50px; }
  &__logo {
    display: flex;
    opacity: 0.2;
    justify-content: center;
    align-items: center;
    p {
      margin-left: 35px;
      font-size: 18px;
      letter-spacing: 0.5px;
      text-align: left;
      color: #bcbcbc;
      text-transform: uppercase;
      span {
        display: block;
        text-transform: none; } } }
  &__logos {
    margin: 40px 0;
    justify-content: space-between;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    img {
      height: 100%; } }
  ul {
    margin-bottom: 65px;
    display: flex;
    justify-content: space-between;
    a {
      font-size: 18px;
      line-height: 1.33;
      text-align: left;
      color: #0d426a;
      text-decoration: none; } } }

